<template>
  <div>
    <vue-txt-number v-model="number"></vue-txt-number>
  </div>
</template>

<script>
import VueTxtNumber from 'vue-txt-number'
export default {
  name: 'txtNumber',
  components: {
    VueTxtNumber
  },
  data () {
    return {
      number: 1
    }
  }
}
</script>

<style>

</style>



